body {
    font-family: 'Fredoka One', sans-serif;
    background-color: white;
}

h1.main-text {
    font-size: 4em;
    margin-bottom: 0;
}

.App {
    text-align: center;
    padding: 0 10px;
    margin: 0 auto;
    max-width: 75rem;
}

img {
    width: 80%;
    max-width: 45rem;
    margin-left: 3.75rem;
}

.ticker {
    font-size: 2em;
    margin-top: 0;
}
@media all and (max-width: 57.5rem) {
    img {
        margin-left: 0;
    }
}

.blurb {
    margin: auto;
    width: 50%;
    text-align: left;
    margin-top: 75px;
}


